header {
  display: flex;
  flex-direction: row;
}
header nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  border: 1px solid black;
}
header nav div.logo {
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 700;
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-right: 1px solid black;
  height: 100%;
  background-color: aqua;
}
header nav div.logo a {
  text-decoration: none;
  color: black;
  border: none;
  text-align: center;
  width: 100%;
  height: 100%;
  border-bottom: 2px solid white;
  box-sizing: border-box;
}
header nav div.logo a:hover {
  background-color: black;
  color: white;
  transition: 0.5s;
}
header nav div.links {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 40%;
}
header nav div.links a {
  display: flex;
  width: 200%;
  align-items: center;
  justify-content: center;
  border-right: 1px solid black;
  height: 100%;
  color: black;
  text-decoration: none;
  border-bottom: 2px solid white;
  box-sizing: border-box;
}
header nav div.links a:hover {
  background-color: black;
  color: white;
  transition: all 0.5s;
}
header nav div.user-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header nav div.user-info button {
  width: 26px;
  height: 26px;
  border: none;
  margin: 10px 20px;
  background-color: transparent;
}
header nav div.user-info button:nth-child(1) {
  background-image: url("../images/login.svg");
}
header nav div.user-info button:last-of-type {
  background-image: url("../images/basket.svg");
}
header nav div.user-info a {
  border: none;
  color: black;
  text-decoration: none;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
}
main h1 {
  text-align: center;
  font-size: 54px;
  background-image: linear-gradient(to bottom, rgb(255, 255, 255), rgba(0, 255, 255, 0.3));
  width: 100%;
  padding: 100px;
}
main p {
  text-align: center;
  font-size: 28px;
  background-color: aqua;
  width: 380px;
}
main nav.nav-bar {
  display: flex;
  justify-content: space-between;
}
main nav.nav-bar div.nav-item {
  width: 20%;
}
main nav.nav-bar div.nav-item img {
  width: 100%;
  height: 100%;
}
main nav.nav-bar div:nth-child(2) {
  width: 60%;
  margin-left: 10px;
  margin-right: 10px;
}
main nav.nav-bar div.nav-item:hover {
  background-image: linear-gradient(to bottom, rgb(17, 222, 147), #0e7ad6);
}
main nav.nav-bar div.nav-item:hover img {
  opacity: 0.8;
}
main a {
  text-decoration: none;
  color: black;
  width: 25%;
}
main div.shop-items {
  display: flex;
  flex-direction: row;
  padding-bottom: 120px;
  width: 1180px;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
main div.shop-items div.shop-item {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  justify-content: center;
}
main div.shop-items div.shop-item img {
  width: 60%;
}
main div.shop-items div.shop-item img:hover {
  transform: scale(1.2);
  transition: 0.5s;
}
main div.shop-items div.shop-item p {
  background: none;
  line-height: 5px;
  width: 60%;
  text-align: left;
}
main button {
  background: black;
  color: aqua;
  border: none;
  width: 200px;
  height: 50px;
  font-size: 24px;
}
main div.special-offers {
  width: 100%;
  padding: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(to bottom, rgb(255, 255, 255), rgba(0, 255, 255, 0.3));
}
main div.special-offers img {
  width: 50px;
}
main div.special-offers h1 {
  line-height: 0px;
  font-size: 72px;
  background: none;
  padding: 0px;
}
main div.special-offers p {
  width: auto;
  background: none;
  line-height: 5px;
}
main div.item-info {
  width: 60%;
  display: flex;
  padding: 100px 0px;
}
main div.item-info form {
  display: flex;
  flex-direction: column;
}
main div.item-info form button.add {
  background: black;
  color: aqua;
  border: none;
  text-align: center;
}
main div.item-info form button {
  cursor: pointer;
  position: relative;
  background: none;
  color: black;
  text-align: left;
  border-bottom: 1px solid black;
  text-transform: uppercase;
}
main div.item-info form button span {
  position: absolute;
  font-size: 20px;
  right: 0;
}
main div.item-info img {
  width: 50%;
  margin-right: 36px;
  border: 1px solid black;
}
main div.item-info h2 {
  font-size: 32px;
  font-weight: 800;
}
main div.item-info p {
  text-align: left;
  width: auto;
  background: none;
  font-size: 18px;
}
main div.item-info input {
  width: 60px;
  height: 30px;
}
main div.item-info button {
  margin-top: 20px;
  font-size: 18px;
  width: 300px;
}
main form.authorization {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding-bottom: 100px;
  align-items: center;
  justify-content: center;
}
main form.authorization input {
  width: 200px;
  padding: 10px;
  margin-top: 30px;
  border-radius: 5px;
  border: none;
  font-size: 16px;
  border-bottom: 1px solid black;
}
main form.authorization input:focus {
  outline: none;
  transition: all 0.5s;
  border: 1px solid black;
}
main form.authorization button {
  width: 220px;
  margin-top: 20px;
  border-radius: 5px;
}
main form.authorization button:hover {
  border: 1px solid black;
  color: black;
  background-color: white;
  transition: 0.5s;
}
main form.authorization a {
  margin-top: 30px;
  width: auto;
}

footer {
  border: 1px solid black;
  width: 100%;
}
footer nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: black;
}
footer nav a {
  text-decoration: none;
  color: black;
}
footer nav div.shop-links {
  margin-left: 5%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
}
footer nav div.shop-links a {
  color: white;
}
footer nav div.shop-links a:nth-child(1) {
  font-size: 30px;
  font-weight: 700;
  text-transform: uppercase;
}
footer nav div.shop-menu {
  background-color: white;
  padding: 5% 10%;
  display: flex;
}
footer nav div.shop-menu div.menu-item {
  display: flex;
  flex-direction: column;
  align-items: left;
  margin-left: 36px;
  justify-content: center;
}
footer nav div.shop-menu div.menu-item p {
  font-weight: 600;
}/*# sourceMappingURL=index.css.map */