header {
    display: flex;
    flex-direction: row;
    nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        border: 1px solid black;
        div.logo {
            text-transform: uppercase;
            font-size: 30px;
            font-weight: 700;
            width: 20%;
            display: flex;
            align-items: center;
            justify-content: center;
            border-right: 1px solid black;
            height: 100%;
            background-color: aqua;
            a {
                text-decoration: none;
                color: black;
                border: none;
                text-align: center;
                width: 100%;
                height: 100%;
                border-bottom: 2px solid white;
                box-sizing: border-box;
            }
            a:hover {
                background-color: black;
                color: white;
                transition: 0.5s;
            }
        }
        div.links {
            display: flex;
            align-items: center;
            justify-content:center;
            height: 100%;
            width: 40%;
            a {
                display: flex;
                width: 200%;
                align-items: center;
                justify-content: center;
                border-right: 1px solid black;
                height: 100%;
                color: black;
                text-decoration: none;
                border-bottom: 2px solid white;
                box-sizing: border-box;
            }
            a:hover {
                background-color: black;
                color: white;
                transition: all 0.5s;
            }
        }
        div.user-info {
            display: flex;
            align-items: center;
            justify-content: space-between;
            button {
                width: 26px;
                height: 26px;
                border: none;
                margin: 10px 20px;
                background-color: transparent;
            }
            button:nth-child(1) {
                background-image: url('../images/login.svg');
            }
            button:last-of-type {
                background-image: url('../images/basket.svg');
            }
            a {
                border: none;
                color: black;
                text-decoration: none;
            }
        }
    }
}
main {
    display: flex;
    flex-direction: column;
    align-items: center;
    h1 {
        text-align: center;
        font-size: 54px;
        background-image: linear-gradient(to bottom, rgb(255,255,255), rgba(0,255,255,0.3));
        width: 100%;
        padding: 100px;
    }
    p {
        text-align: center;
        font-size: 28px;
        background-color: aqua;
        width: 380px;
    }
    nav.nav-bar {
        display: flex;
        justify-content: space-between;
        div.nav-item {
            width: 20%;
            img {
                width: 100%;
                height: 100%;
            }
        }
        div:nth-child(2) {
            width: 60%;
            margin-left: 10px;
            margin-right: 10px;
        }
        div.nav-item:hover {
            background-image: linear-gradient(to bottom, rgb(17, 222, 147), #0e7ad6);
            img{
                opacity: 0.8;
            }
        }
    }
    a{
        text-decoration: none;
        color: black;
        width: 25%;
    }
    div.shop-items {
        display: flex;
        flex-direction: row;
        padding-bottom: 120px;
        width: 1180px;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        div.shop-item {
            display: flex;
            flex-direction: column;
            width: 100%;
            align-items: center;
            justify-content: center;
            img {
                width: 60%;
            }
            img:hover {
                transform: scale(1.2);
                transition: 0.5s;
            }
            p {
                background: none;
                line-height: 5px;
                width: 60%;
                text-align: left;
            }
        }
    }
    button {
        background: black;
        color:aqua;
        border: none;
        width: 200px;
        height: 50px;
        font-size: 24px;
    }
    div.special-offers {
        width: 100%;
        padding: 200px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background-image: linear-gradient( to bottom, rgb(255,255,255), rgba(0,255,255,0.3));
        img {
            width: 50px;
        }
        h1 {
            line-height: 0px;
            font-size: 72px;
            background: none;
            padding: 0px;
        }
        p {
            width: auto;
            background: none;
            line-height: 5px;
        }
    }
    div.item-info {
        width: 60%;
        display: flex;
        padding: 100px 0px;
        form {
            display: flex;
            flex-direction: column;
            button.add {
                background: black;
                color: aqua;
                border: none;
                text-align: center;
            }
            button {
                cursor: pointer;
                position: relative;
                background: none;
                color: black;
                text-align: left;
                border-bottom: 1px solid black;
                text-transform: uppercase;
                span {
                    position: absolute;
                    font-size: 20px;
                    right: 0;
                }
            }
        }
        img {
            width: 50%;
            margin-right: 36px;
            border: 1px solid black;
        }
        h2{
            font-size: 32px;
            font-weight: 800;
        }
        p {
            text-align: left;
            width: auto;
            background: none;
            font-size: 18px;
        }
        input {
            width: 60px;
            height: 30px;
        }
        button {
            margin-top: 20px;
            font-size: 18px;
            width: 300px;
        }
    }
    form.authorization {
        width: 100%;
        display: flex;
        flex-direction: column;
        padding-bottom: 100px;
        align-items: center;
        justify-content: center;
        input {
            width: 200px;
            padding: 10px;
            margin-top: 30px;
            border-radius: 5px;
            border:none;
            font-size: 16px;
            border-bottom: 1px solid black;
        }
        input:focus {
            outline: none;
            transition: all 0.5s;
            border: 1px solid black;
        }
        button {
            width: 220px;
            margin-top: 20px;
            border-radius: 5px;
        }
        button:hover {
            border:1px solid black;
            color: black;
            background-color: white;
            transition: 0.5s;
        }
        a {
            margin-top: 30px;
            width: auto;
        }
    }
}

footer {
    border: 1px solid black;
    width: 100%;
    nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: black;
        a {
            text-decoration: none;
            color:black;
        }
        div.shop-links {
            margin-left: 5%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: left;
            a {
                color: white;
            }
            a:nth-child(1) {
                font-size: 30px;
                font-weight: 700;
                text-transform: uppercase ;
            }
        }
        div.shop-menu {
            background-color: white;
            padding: 5% 10%;
            display: flex;
            div.menu-item {
                display: flex;
                flex-direction: column;
                align-items: left;
                margin-left: 36px;
                justify-content: center;
                p {
                    font-weight: 600;
                }
            }
        }
    }
}